import React, { Component } from 'react';
import { object } from 'prop-types';

import Page from '../components/Page';
import List from '../components/Post/List';
import Pagination from '../components/Navigation/Pagination';

export default class BlogList extends Component {

  static propTypes = {
    route: object,
  };

  getUrl = page => {
    if (page === 1) {
      return '/';
    }
    return `/page/${page}`;
  };

  render() {
    const { posts, latest, page, total } = this.props.pathContext;

    return (
      <Page
        title={page === 1 ? 'Авторский блог' : `Страница ${page}`}
        posts={posts}
        path={this.getUrl(page)}
      >
        <List
          hero={page === 1 ? posts[0] : null}
          mediumList={latest.slice(0, 3)}
          smallList={posts}
        />
        <Pagination current={page} total={total} getUrl={this.getUrl} />
      </Page>
    );
  }
};



